@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@300;400;600&display=swap');

:root {
    --very-dark-blue-main-bg: hsl(217, 54%, 11%);
    --very-dark-blue-card-bg: hsl(216, 50%, 16%);
    --very-dark-blue-line: hsl(215, 32%, 27%);
    --color-white: hsl(0, 0%, 100%);

    --soft-blue: hsl(215, 51%, 70%);
    --cyan: hsl(178, 100%, 50%);
}

*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: "Outfit", sans-serif;
    color: var(--color-white);
    width: 100%;
    height: 100%;
}

.App {
    background: var(--very-dark-blue-main-bg);
    font-size: 16px;
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}


.container {
    background-color: var(--very-dark-blue-card-bg);
    padding: 10px;
    border-radius: 0.5rem;
    width: 20rem;
    height: 35rem;
}

.container-img {
    max-width: 100%;
    border-radius: 0.5em;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
}

.container-h1 {
    font-size: 18px;
    color: var(--color-white);
    margin-top: 10px;
    margin-bottom: 10px;
    cursor: pointer;
}

.container-h1:hover {
    color: var(--cyan);
    transition: all .3s;
}

.container-p {
    font-size: 14px;
    font-weight: 300;
    color: hsl(0, 0%, 100%, 50%);
}


.icon-block {
    width: 100%;

    ul {
        list-style: none;
        display: flex;
        width: 100%;
        justify-content: space-between;
        align-items: center;
        padding: 1.5rem 0;
        border-bottom: 2px solid var(--very-dark-blue-line);
    }
}

.icon-block ul li {
    display: flex;

}

.blue {
    color: var(--cyan);
}

.white {
    color: hsl(0, 0%, 100%, 50%);
}

.icon-block span {
    margin-left: 10px;
}

.container-bottom {
    margin-top: 10px;
    display: flex;
    align-items: center;
}

.container-bottom span {
    color: var(--soft-blue);
}

.container-bottom a {
    text-decoration: none;
    color: var(--color-white);
    margin-left: 5px;
    font-weight: 300;

}

.container-bottom a:hover {
    color: var(--cyan);
    transition: all .3s;
}

.img-avatar {
    width: 50px;
    border-radius: 50%;
    border: 2px solid var(--color-white);
    margin-right: 10px;
}

.equilibrium {
    position: relative;
}

.equilibrium-hover {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    z-index: 10;
    cursor: pointer;
    opacity: 0;
}

.equilibrium-hover:hover {

    background: hsla(178, 100%, 50%, 50%);
    opacity: 1;
    transition: all .5s ease-in-out;

}

.equilibrium-hover img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    opacity: 1;
    z-index: 1000;
}

.attribution {
    margin-top: 20px;
    font-size: 11px;
    text-align: center;
}

.attribution a {
    color: hsl(228, 45%, 44%);
}

